    return {
      dayButtonActive: 1,
    };
  },
  computed: {
    programList() {
      const list = Array(10).fill({
        time: "09:00",
        text:
          "Заголовок. Ut enim ad minim veniam, quis nostrud exercitation \n" +
          "ullamco laboris nisi ut aliquip ex ea commodo consequat.",
        author: "Имя Фамилия",
        place: "Место проведения, Конференц зал 1",
        itSpeaker: false,
      });
      return list;
    },
  },
});
